<template>
  <div class="page">
   <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    :title="title"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content change-nickname">
      <label for="phone">
        <div class="title">手机号</div>
        <input type="tel" id="phone" class="input" placeholder="输入手机号码" v-model="form.phone">
      </label>
      <div class="division"></div>
      <button class="button button-clear btn-get-code"
          :class="{'button-royal disabled': getCodeLock, 'button-dark': !getCodeLock}"
          @click="sendCode()" v-text="getCodeText"></button>
      <div class="bottom-line"></div>
      <label for="code">
        <div class="title">验证码</div>
        <input type="tel" id="code" class="input" placeholder="输入验证码" v-model="form.code">
        <div class="bottom-line last"></div>
      </label>

      <button class="button button-submit" :class="{'can-submit': phoneOK && codeOK, 'button-disabled': !phoneOK || !codeOK}" @click="submit()">提交</button>
      <p v-if="$route.params.status == '0'" class="link-agreement" @click="showAgreement()">
        绑定手机视为您已同意<span>《用户服务协议》</span>
      </p>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .change-nickname {
    background: #FFFFFF;
    padding-top: 75px;
    padding-left: 30px;
    color: #b5b5b5;

    @include font-regular();

    .title {
      font-size: 12px;
      height: 17px;
      line-height: 17px;
    }

    .input {
      width: 200px;
      height: 33px;
      font-size: 24px;
      line-height: 33px;
      margin-bottom: 3px;
      display: inline-block;
      padding: 0;

      @include font-regular();

      &::-webkit-input-placeholder {
        line-height: 33px;
        height: 33px;
        color: #DDDDDD;

        @include font-regular();
      }
    }

    .division {
      position: absolute;
      top: 100px;
      right: 116px;
      width: 1px;
      height: 17px;
      background: #DDDDDD;
    }

    .btn-get-code {
      position: absolute;
      padding: 0;
      height: 20px;
      font-size: 14px;
      line-height: 20px;
      min-height: 20px;
      top: 98px;
      right: 30px;

      @include font-regular();
    }

    .bottom-line {
      width: 200px;
      height: 1px;
      background: #DDDDDD;
      margin-bottom: 30px;

      &.last {
        margin-bottom: 60px;
      }
    }

    .button-submit {
      width: 172px;
      height: 40px;
      min-height: 40px;
      line-height: 40px;
      font-size: 13px;
      font-weight: 500;
      color: #FFFFFF;
      margin-bottom: 206px;
      border-radius: 2px;

      @include font-semibold();

      &.button-disabled {
        background: #DDDDDD;
      }

      &.can-submit {
        background-color: #ef502b;
      }
    }

    .link-agreement {
      height: 20px;
      font-size: 14px;
      color: #b5b5b5;

      span {
        color: #4a90e2;
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {popup_choose_verify_code_type, popup_has_bind_mobile} from '../popup.js'
  import Agreement from '../../Agreement.vue'
  import {mapActions} from 'vuex'

  const countDownLimit = 60
  var formatLock = false

  export default {
    components: {
      AnimateHeader
    },

    data () {
      return {
        title: this.$route.params.status === '0' ? '绑定手机' : '修改绑定',
        form: {
          phone: '',
          code: ''
        },

        getCodeLock: false,
        getCodeText: '获取验证码',
        sec: 0,

        // 防止二次提交
        submitLock: false,

        // 数据是否符合校验
        phoneOK: false,
        codeOK: false,
        agreement: undefined,
        voiceVerify: false
      }
    },

    watch: {
      'form.phone': function (val) {
        if (formatLock) return
        formatLock = true
        // 格式化
        let v = val.replace(/\s/g, '')
        this.form.phone = _.filter([v.substr(0, 3), v.substr(3, 4), v.substr(7, 4)], function (value) { return value !== '' }).join(' ')
        formatLock = false

        if (!/^1[23456789][\d]{9}$/.test(val.replace(/\s/g, ''))) {
          this.phoneOK = false
        } else {
          this.phoneOK = true
        }

        if (val.length > 13) this.form.phone = val.substr(0, 13)
      },

      'form.code': function (val) {
        if (!val) {
          this.codeOK = false
        } else if (!/^[\d]{4}$/.test(val)) {
          this.codeOK = false
        } else {
          this.codeOK = true
        }

        if (val.length > 4) this.form.code = val.substr(0, 4)
      }
    },

    mounted () {
      $modal.fromComponent(Agreement, {
        // title: '用户服务协议',
        // theme: 'dark'
      }).then((modal) => {
        this.agreement = modal
      })

      $bus.$on('closeAgreementModal', () => {
        this.agreement.hide()
      })
    },

    destroyed () {
      if (this.agreement) {
        $modal.destroy(this.agreement)
      }
    },

    methods: {
      sendCode () {
        if (this.voiceVerify) {
          this.popCodeType()
        } else {
          this.getCode(1)
        }
      },

      getCode (type) {
        // 防止按钮被快速点击两次
        if (this.getCodeLock) return
        this.getCodeLock = true

        // 处于倒计时中
        if (this.sec > 0) return

        // 校验
        let phone = this.form.phone.replace(/\s/g, '')
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        }

        if (tips) {
          setTimeout(() => { this.getCodeLock = false }, 1000)
          $toast.show(tips)
          return
        }
        this.voiceVerify = true

        this.bindMobileSendCode({phone: phone, type: type})
          .then((res) => res.data && res.data.err_code)
          .then(err_code => {
            if (err_code === 40003) {
              let popup = $popup.fromTemplate(
                popup_has_bind_mobile.template,
                popup_has_bind_mobile.options
              )
              popup.show().then((buttonIndex) => {
                if (buttonIndex === 1) {
                  let path = {
                    path: '/login',
                    query: {backroute: '/user/bindMobile/' + this.$route.params.status}
                  }
                  $router.forward(path)
                } else {
                  this.getCodeLock = false
                }
              })
            } else {
              this.sec = countDownLimit
              this.countDown()
            }
          })
          .catch(() => {
            this.getCodeLock = false
          })
      },

      check_input () {
        let phone = this.form.phone.replace(/\s/g, '')
        let code = this.form.code
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        } else if (!code) {
          tips = '验证码不能为空'
        } else if (!/^[\d]{4}$/.test(code)) {
          tips = '验证码格式不正确'
        }

        return tips
      },

      submit () {
        if (this.submitLock) return
        this.submitLock = true
        setTimeout(() => { this.submitLock = false }, 1000)

        if (!this.phoneOK || !this.codeOK) return

        let tips = this.check_input()
        if (tips) {
          $toast.show(tips)
          return
        }

        formatLock = true
        let phone = this.form.phone.replace(/\s/g, '')
        let code = this.form.code

        this.bindMobile({phone: phone, code: code})
          .then(() => {
            formatLock = false
          })
      },

      countDown () {
        if (this.sec) {
          this.sec--
          this.getCodeText = this.sec ? this.sec + 's' : '发送验证码'
          setTimeout(() => {
            this.countDown()
          }, 1000)
        } else {
          this.getCodeLock = false
        }
      },

      showAgreement () {
        this.agreement.show()
      },

      popCodeType () {
        let popup = $popup.fromTemplate(
            popup_choose_verify_code_type.template,
            popup_choose_verify_code_type.options
          )
        popup.show().then((index) => {
          if (index === 0) {
            this.getCode(1)
          }
          if (index === 1) {
            this.getCode(2)
          }
        })
      },

      back () {
        let path = _.get(this.$route.query, 'backroute', '/user/profile')
        $router.back({ path })
      },

      ...mapActions('user', [
        'bindMobileSendCode',
        'bindMobile'
      ])
    }
  }
</script>



